export default {
	data() {
		return {
			userinfo:[]
		}
	},
	onLoad() {
		
		var userinfo = uni.getStorageSync("globalUser");
		this.userinfo = userinfo;
		//console.log(this.userinfo)
	},
	methods: {
		modifySex(){
			uni.navigateTo({
				url:"../mesex/mesex"
			})
		},
		//修改昵称事件
		modifyNickname(){
			uni.navigateTo({
				url:"../meNickname/meNickname"
			})
		},
		modifyBirthday(){
			uni.navigateTo({
				url:"../mebirthday/mebirthday"
			})
		},
		//头像点击事件
		operator(){
			var globalUser = this.userinfo;
			//调出操作菜单
			uni.showActionSheet({
				itemList:["查看头像","上传头像"],
				success(res) {
					//获得点击操作菜单的下标
					var index = res.tapIndex;
					//console.log(index);
					if(index == 0){
						//预览头像
						var faceArr = [];
						faceArr.push(globalUser.facepic)
						uni.previewImage({
							urls: faceArr,
							current:faceArr[0]
						})
					}
					else if(index == 1){
						//调用上传图片的上传
						uni.chooseImage({
							count:1,
							sizeType:["compressed"],
							sourceType:["album"],
							success(res) {
								//获得临时路径
								//console.log(res)
								var tempFilepath = res.tempFilePaths[0];
								uni.navigateTo({
									url:"../meFace/meFace?tempFilepath="+ tempFilepath
								})
							}
						})
						//上传头像
						
					}
				}
			})
		},
		
		//清理缓存事件
		clearStorage(){
			uni.clearStorageSync();
			uni.showToast({
				title:"清理成功",
				mask:false,
				duration: 1500
			})
		},
		tuichu(){
			uni.removeStorageSync("globalUser");
			uni.navigateBack();
			
		}
	}
}
